import React from 'react';
import { FiUsers, FiCheckCircle, FiClock, FiMapPin, FiDownload } from 'react-icons/fi';
import { format } from 'date-fns';

const DashboardStats = ({ stats }) => {
  if (!stats) {
    return (
      <div className="text-center py-12">
        <div className="w-8 h-8 border-4 border-primary-200 border-t-primary-600 rounded-full animate-spin mx-auto mb-4"></div>
        <p className="text-gray-600">Loading statistics...</p>
      </div>
    );
  }

  const getAadhaarStats = () => {
    const statsMap = {};
    stats.aadhaarStats?.forEach(item => {
      statsMap[item._id] = item.count;
    });
    return statsMap;
  };

  const aadhaarStats = getAadhaarStats();

  return (
    <div className="space-y-6">
      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <FiUsers className="h-8 w-8 text-primary-600" />
            </div>
            <div className="ml-5 w-0 flex-1">
              <dl>
                <dt className="text-sm font-medium text-gray-500 truncate">
                  Total Volunteers
                </dt>
                <dd className="text-lg font-medium text-gray-900">
                  {stats.totalVolunteers || 0}
                </dd>
              </dl>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <FiCheckCircle className="h-8 w-8 text-green-600" />
            </div>
            <div className="ml-5 w-0 flex-1">
              <dl>
                <dt className="text-sm font-medium text-gray-500 truncate">
                  Complete Profiles
                </dt>
                <dd className="text-lg font-medium text-gray-900">
                  {stats.completeProfiles || 0}
                </dd>
              </dl>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <FiClock className="h-8 w-8 text-yellow-600" />
            </div>
            <div className="ml-5 w-0 flex-1">
              <dl>
                <dt className="text-sm font-medium text-gray-500 truncate">
                  Incomplete Profiles
                </dt>
                <dd className="text-lg font-medium text-gray-900">
                  {stats.incompleteProfiles || 0}
                </dd>
              </dl>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <FiMapPin className="h-8 w-8 text-blue-600" />
            </div>
            <div className="ml-5 w-0 flex-1">
              <dl>
                <dt className="text-sm font-medium text-gray-500 truncate">
                  With Location
                </dt>
                <dd className="text-lg font-medium text-gray-900">
                  {stats.totalVolunteers - (stats.incompleteProfiles || 0)}
                </dd>
              </dl>
            </div>
          </div>
        </div>
      </div>

      {/* Aadhaar Status Breakdown */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
        <h3 className="text-lg font-medium text-gray-900 mb-4">Aadhaar Status Breakdown</h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="bg-green-50 rounded-lg p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-green-800">Have Aadhaar</p>
                <p className="text-2xl font-bold text-green-900">
                  {aadhaarStats.yes || 0}
                </p>
              </div>
              <FiCheckCircle className="h-8 w-8 text-green-600" />
            </div>
          </div>

          <div className="bg-red-50 rounded-lg p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-red-800">No Aadhaar</p>
                <p className="text-2xl font-bold text-red-900">
                  {aadhaarStats.no || 0}
                </p>
              </div>
              <FiUsers className="h-8 w-8 text-red-600" />
            </div>
          </div>

          <div className="bg-gray-50 rounded-lg p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-800">Unknown</p>
                <p className="text-2xl font-bold text-gray-900">
                  {aadhaarStats.unknown || 0}
                </p>
              </div>
              <FiClock className="h-8 w-8 text-gray-600" />
            </div>
          </div>
        </div>
      </div>

      {/* Recent Submissions */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-medium text-gray-900">Recent Submissions</h3>
          <a
            href="/admin/volunteers"
            className="text-sm text-primary-600 hover:text-primary-500"
          >
            View all â†’
          </a>
        </div>
        
        {stats.recentSubmissions && stats.recentSubmissions.length > 0 ? (
          <div className="space-y-3">
            {stats.recentSubmissions.map((submission, index) => (
              <div key={index} className="flex items-center justify-between py-3 border-b border-gray-100 last:border-b-0">
                <div className="flex items-center space-x-3">
                  <div className="w-8 h-8 bg-primary-100 rounded-full flex items-center justify-center">
                    <FiUsers className="w-4 h-4 text-primary-600" />
                  </div>
                  <div>
                    <p className="text-sm font-medium text-gray-900">
                      {submission.userId?.email || 'Unknown'}
                    </p>
                    <p className="text-xs text-gray-500">
                      Submitted {submission.submittedAt ? format(new Date(submission.submittedAt), 'MMM dd, yyyy') : 'Recently'}
                    </p>
                  </div>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                    Complete
                  </span>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-8">
            <FiUsers className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">No recent submissions</h3>
            <p className="mt-1 text-sm text-gray-500">
              New volunteer submissions will appear here.
            </p>
          </div>
        )}
      </div>

      {/* Quick Actions */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
        <h3 className="text-lg font-medium text-gray-900 mb-4">Quick Actions</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <a
            href="/admin/volunteers"
            className="flex items-center justify-between p-4 border border-gray-200 rounded-lg hover:border-primary-300 hover:bg-primary-50 transition-colors"
          >
            <div className="flex items-center space-x-3">
              <FiUsers className="h-6 w-6 text-primary-600" />
              <div>
                <p className="text-sm font-medium text-gray-900">View All Volunteers</p>
                <p className="text-xs text-gray-500">Manage volunteer profiles</p>
              </div>
            </div>
            <FiDownload className="h-5 w-5 text-gray-400" />
          </a>

          <a
            href="/admin/volunteers/export/csv"
            className="flex items-center justify-between p-4 border border-gray-200 rounded-lg hover:border-primary-300 hover:bg-primary-50 transition-colors"
          >
            <div className="flex items-center space-x-3">
              <FiDownload className="h-6 w-6 text-primary-600" />
              <div>
                <p className="text-sm font-medium text-gray-900">Export Data</p>
                <p className="text-xs text-gray-500">Download volunteer data as CSV</p>
              </div>
            </div>
            <FiDownload className="h-5 w-5 text-gray-400" />
          </a>
        </div>
      </div>
    </div>
  );
};

export default DashboardStats; 